.image-text-container {
    width: 80%;

    display: flex;
    justify-content: space-between;
    flex-direction: row;
    align-items: flex-start;
}

.image-text-image {
    width: 40%;
}

.image-text-text {
    width: 50%;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
}

.image-text-subtitle {
    font-size: 50px;
}

.image-text-body {
    font-size: 25px;
    width: 100%;
    text-wrap: wrap;
    text-align: justify;
    text-justify: inter-word;
}