
.intro-container { 
    position: relative;
    height: 1000px;
    text-align: center;
    overflow: hidden;
}

.intro-background-container {
    display: flex;
    align-items: center;
    flex-direction: column;
}

.intro-background-image {
    width: 2000px;
    position: relative;
    top: -100px;
}

.intro-name-container {
    margin-top: 20%;
}

.intro-subtitle {
    font-size: 30px;
}

.intro-name {
    font-size: 100px;
    font-weight: light;
    color: black;
    text-decoration: none;
}
